.preloader2{flex:0 0 auto;width:300px;height:300px;position:relative;filter:url("#goo")}@media only screen and (max-width: 991px){.preloader2{display:none}}.loader2{filter:url("#goo");width:3rem;height:3rem;position:absolute;top:calc(30% - 3rem / 2);left:calc(30% - 3rem / 2);background:#3f4c6b;border-radius:1.5rem}.loader-left{animation:rotateLeft 4s ease-in-out infinite,color 4s ease-in-out infinite}.loader-right{animation:rotateRight 4s ease-in-out infinite,color 4s ease-in-out infinite}@keyframes rotateLeft{0%{transform:rotate(0deg)}100%{transform:rotate(360deg);transform-origin:-50% -50%}}@keyframes rotateRight{0%{transform:rotate(0deg)}100%{transform:rotate(360deg);transform-origin:150% 150%}}@keyframes color{0%{background:#3f4c6b}50%{background:#FFF5A5}100%{background:#3f4c6b}}
